<template>
	<div id="app">
		<load v-if="loading"></load>
		<over v-if="over"></over>
		<template v-if="!over">
			<div id="banner" ref="box"></div>
		</template>
	</div>
</template>
<style src="./css/app.scss" lang="sass" ></style>
<script>
	import load from "./components/load"
	import over from "./components/over"
	import data from "./js/data"
	
	export default { 
		data: function () {
			return {
				plat: window.pt,
				loading: true,
				over: false,
				isLogin: true,
				isguest: false,
			}
		},
		computed: {

		},
		created() {
			this.initPage()
		},
		mounted() {},
		methods: {
			initPage() {
				if (process.env.NODE_ENV !== "development" && process.env.NODE_ENV !== "production") {
					this.loading = false
					console.log(data)
				} else {
					this.local.reqaObj(this.common.server() + "pkg170801/init", data => {
						console.log(data)
						this.loading = false
						if (data.code == -4) {
							this.over = true
							return
						}

					})
				}
			},
			move(e){
				e.target.style.animationPlayState="paused"
			}
		},
		components: {
			load,
			over
		}
	}
</script>


